import React from 'react';
import Square from "./Components/Square";
import {Avatar, Button, Chip, FormControl, Grid, Input, InputAdornment, InputLabel} from "@material-ui/core";
import SwalWinner from 'sweetalert2';
import SwalDraw from 'sweetalert2';
import {calculateWinner} from "./utilities/helper";


class Game extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            squares: Array(8).fill(null), //Das Board besteht aus 9 squares, die zu Beginn leer sind
            XIsNextPlayer: true,
            numberOfLeftSteps: 9,
        };
    }

    renderSquare(i) {
        this.handleClick(i)
        return (
            <Square
                value={this.state.squares[i]}/> //Jeder Square hat nun einen eigenen Wert (X, O, null)
        );
    }

    restartGame = () => {
        this.setState({squares: [],});
        this.setState({numberOfLeftSteps: 9});
    };

    handleClick(i) {
        const squares = this.state.squares.slice(); //returned Elemente in Array als ein neues Array
        //console.log(squares);
        squares[i] = this.state.XIsNextPlayer ? 'ð™“' : 'ð™Š';
        this.setState({
            squares: squares,
            XIsNextPlayer: !this.state.XIsNextPlayer,
            numberOfLeftSteps: this.state.numberOfLeftSteps - 1,
        });
    }

    render() {
        const winner = calculateWinner(this.state.squares);

        if (winner) {
            SwalWinner.fire({
                title: 'Congratulations!',
                text: 'Player ' + winner + ' won the game.',
                icon: 'success',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Restart!'
            }).then((result) => {
                    if (result.isConfirmed) {
                        this.restartGame();
                        SwalDraw.close();
                    } else if (result.dismiss === SwalWinner.DismissReason.cancel) {
                    }
                })
        }

    if (!winner && this.state.numberOfLeftSteps === 0) {
        SwalDraw.fire({
            title: 'Good luck next time.',
            text: 'Draw. Nobody won the game.',
            icon: 'error',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Try again!'
        }).then((result) => {
                if (result.isConfirmed) {
                   this.restartGame();
                   SwalDraw.close();
                } else if (result.dismiss === SwalWinner.DismissReason.cancel) {
                }
            })
    }


    return(

<div className="App">
<div className="board">

<Grid container>
<Grid container item xs= {4}>
<Square value={this.state.squares[0]} squareId={0}
        passedFunction={() => {
            this.renderSquare(0)
        }}/>
</Grid>

<Grid container item xs={4}>
    <Square value={this.state.squares[1]} squareId={1}
            passedFunction={() => {
                this.renderSquare(1)
            }}/>
</Grid>

<Grid container item xs={4}>
    <Square value={this.state.squares[2]} squareId={2}
            passedFunction={() => {
                this.renderSquare(2)
            }}/>
</Grid>

</Grid>

<Grid container>
    <Grid container item xs={4}>
        <Square value={this.state.squares[3]} squareId={3}
                passedFunction={() => {
                    this.renderSquare(3)
                }}/>
    </Grid>

    <Grid container item xs={4}>
        <Square value={this.state.squares[4]} squareId={4}
                passedFunction={() => {
                    this.renderSquare(4)
                }}/>
    </Grid>

    <Grid container item xs={4}>
        <Square value={this.state.squares[5]} squareId={5}
                passedFunction={() => {
                    this.renderSquare(5)
                }}/>
    </Grid>
    <FormControl size={1}>
        <InputLabel htmlFor="input-with-icon-adornment">Moves left: </InputLabel>
        <Input
            readOnly={true}
            id="input-with-moves-left"
            startAdornment={
                <InputAdornment position="start">
                    {this.state.numberOfLeftSteps}
                </InputAdornment>
            }
        />
    </FormControl>
</Grid>

<Grid container>
    <Grid container item xs={4}>
        <Square value={this.state.squares[6]} squareId={6}
                passedFunction={() => {
                    this.renderSquare(6)
                }}/>
    </Grid>

    <Grid container item xs={4}>
        <Square value={this.state.squares[7]} squareId={7}
                passedFunction={() => {
                    this.renderSquare(7)
                }}/>
    </Grid>

    <Grid container item xs={4}>
        <Square value={this.state.squares[8]} squareId={8}
                passedFunction={() => {
                    this.renderSquare(8)
                }}/>
    </Grid>
</Grid>
<Chip variant="outlined" color="primary" label="is the next player"
      style={{width: '50%', height: '10%', marginLeft: '1%'}}
      avatar={<Avatar>{this.state.XIsNextPlayer ? 'X' : 'O'}</Avatar>}/>
<Button size="small" color="primary" variant="contained" style={{marginRight: '90%', marginLeft: '60%', marginTop: '-20%'}}
        onClick={this.restartGame}> Restart </Button>
</div>
</div>


)};
}

export default Game;